.footer_wrap {
  display: flex;
  justify-content: space-between;
}
.footer_contacts {
  display: flex;
  gap: 20px;
}

.footer_link {
  margin: auto 0;
  color: rgb(66, 68, 110);
  font-size: 18px;
  font-weight: 400;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-bottom 0.3s ease-in-out;
}
.footer_link:hover {
  border-bottom: 1px solid currentColor;
}
.line {
  border: 2px solid rgb(102, 102, 102);
  opacity: 0.3;
  width: 100%;
  margin: 50px 0;
}
.darkLine {
  border: 2px solid white;
  opacity: unset;
}
.footer_foot {
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;
}

.cretors_name,
.creators {
  text-align: center;
  font-size: 18px;
  font-weight: 400;
}

.cretors_name:nth-child(1) {
  background: linear-gradient(to right, rgb(19, 79, 245), rgb(105, 36, 202));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}
.cretors_name:nth-child(2) {
  background: linear-gradient(to right, rgb(105, 36, 202), rgb(180, 36, 202));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}
.cretors_name:nth-child(3) {
  background: linear-gradient(to right, rgb(180, 36, 202), rgb(194, 36, 202));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}
.cretors_name:nth-child(4) {
  background: linear-gradient(to right, rgb(180, 36, 202), rgb(231, 15, 170));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}
.footer_contacts_dark a {
  color: white;
}

.footer_desktop {
  padding: 0 20px;
}
.footer_foot_desktop {
  flex-direction: column;
  gap: 20px;
  align-items: center;
}
.footer_wrap_mobile {
  flex-direction: column-reverse;
  gap: 10px;
  align-items: center;
}
.footer_contacts_mobile {
  flex-direction: column;
  gap: 10px;
  align-items: center;
}
